import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'
import { Message } from 'element-ui'
import { getToken, setToken, removeToken } from '@/utils/cache'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css' // progress bar style

Vue.use(VueRouter)
import AdminLayout from "@/views/Admin/Layout/index.vue"

const routes = [
    {
        path: "/", component: () => import("@/components/HelloWorld.vue")
    },
    {
        path: "/Login", component: () => import("@/views/Login.vue")
    },
    {
        path: "/Register", component: () => import("@/views/Register.vue")
    },

]
export const adminRouters = [{
    path: "/Admin",
    redirect: "/Admin/Home",
    component: AdminLayout,
    meta: {
        title: "控制台",
        isAdmin: true,

    },
    children: [
        {
            path: "/Admin/Home",
            meta: {
                isAdmin: true,
            },
            component: () => import("@/views/Admin/Home")
        },
        {
            path: "/Admin/UserList",
            meta: {
                title: "用户信息",
                isAdmin: true,
            },
            component: () => import("@/views/Admin/UserList")
        },
        {
            path: "/Admin/UserPerson",
            meta: {
                title: "个人信息",
                isAdmin: true,
            },
            component: () => import("@/views/Admin/UserPerson")
        },
        {
            path: "/Admin/PasswordEdit",
            meta: {
                title: "修改密码",
                isAdmin: true,
            },
            component: () => import("@/views/Admin/PasswordEdit")
        },

      	 {
            path: "/Admin/AppointRecordList",
            meta: {
                title: "预约记录",
                isAdmin: true,
            },
            component: () => import("@/views/Admin/AppointRecordList")
        },	
 
      	 {
            path: "/Admin/BannerList",
            meta: {
                title: "封面",
                isAdmin: true,
            },
            component: () => import("@/views/Admin/BannerList")
        },	
 
      	 {
            path: "/Admin/IntegralList",
            meta: {
                title: "积分",
                isAdmin: true,
            },
            component: () => import("@/views/Admin/IntegralList")
        },	
 
      	 {
            path: "/Admin/RoomList",
            meta: {
                title: "自习室",
                isAdmin: true,
            },
            component: () => import("@/views/Admin/RoomList")
        },	
 
      	 {
            path: "/Admin/SeatList",
            meta: {
                title: "座位",
                isAdmin: true,
            },
            component: () => import("@/views/Admin/SeatList")
        },	
 


    ]

},
]

const router = new VueRouter({
    routes: [...routes, ...adminRouters] // (缩写) 相当于 routes: routes
})


// 编写导航路由
router.beforeEach(async (to, from, next) => {

    NProgress.start()
    const hasToken = getToken()
    console.log(hasToken)
    if (hasToken) {

        if (store.getters.HasUserInfo == false) {
            await store.dispatch('GetInfo');
        }
        //如果去往的页面是后台
        if (to.meta && to.meta.isAdmin) {
            if (store.getters.RoleType == "管理员") {
                next()
            }
            else {
                next({ path: "/" })
            }
        }
        else {
            if (store.getters.RoleType == "管理员") {
                next({ path: "/Admin" })
            }
            else {
                next()
            }
        }

    }
    else {
        if (to.meta && to.meta.isAdmin) {
            next({ path: "/Login" })
        }
        else {
            next()
        }
    }
    NProgress.done()

})
router.afterEach(() => {
    // finish progress bar
    NProgress.done()
})


export default router;